.Opcion {
  border: groove;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  margin: 20px;
}
.Opcion img {
  border-radius: 50px;
  height: 120px;
  padding: 13px;
  width: 120px;
}

.Boton {
  position: absolute;
  display: none;
  margin-top: -40px;
  text-align-last: center;
  padding-top: 10rem;
  color: white;
  width: 100%;
  height: 79%;

  background-color: rgb(208 150 150 / 73%);
}
.Boton h3 {
  font-size: 45px;
}
.Boton::before {
  z-index: -1;
  opacity: 0.7;
}
.Container_components {
  display: flex;
  align-content: flex-end;
  justify-content: center;
  text-align: center;
}
h1 {
  text-align: center;
  font-size: 3em;
  font: bold 4.5vw/1.6 "Signika", sans-serif;
}

h1 span {
  display: inline-block;
  animation: float 0.2s ease-in-out infinite;
}
@keyframes float {
  0%,
  100% {
    transform: none;
  }
  33% {
    transform: translateY(-1px) rotate(-2deg);
  }
  66% {
    transform: translateY(1px) rotate(2deg);
  }
}
body:hover span {
  animation: bounce 0.6s;
  z-index: 3;
}
@keyframes bounce {
  0%,
  100% {
    transform: translate(0);
  }
  25% {
    transform: rotateX(20deg) translateY(2px) rotate(-3deg);
  }
  50% {
    transform: translateY(-20px) rotate(3deg) scale(1.1);
  }
}
button {
  font-size: 20px;
}
span:nth-child(4n) {
  color: hsl(50, 75%, 55%);
  text-shadow: 1px 1px hsl(50, 75%, 45%), 2px 2px hsl(50, 45%, 45%),
    3px 3px hsl(50, 45%, 45%), 4px 4px hsl(50, 75%, 45%);
}
span:nth-child(4n-1) {
  color: hsl(135, 35%, 55%);
  text-shadow: 1px 1px hsl(135, 35%, 45%), 2px 2px hsl(135, 35%, 45%),
    3px 3px hsl(135, 35%, 45%), 4px 4px hsl(135, 35%, 45%);
}
span:nth-child(4n-2) {
